.pagination__page {
    list-style-type: none;
    font-size: 18px;
    cursor: pointer;
    transition: color 0.2s linear;
}

.pagination__page:hover {
    border-bottom: 1px solid;
    color: #457af5;
}

.pagination__page.disabled:hover {
    border-bottom: none;
    color: #808080;
}